import React, {Component} from 'react';
import './StylesNoticias.css';
import { IoIosPaper } from "react-icons/io";


export default class Noticias extends Component{
    render(){
        return(
            <div id="containerNoticias">
                <div className="containerTituloNoticias">
                    <IoIosPaper className="iconNoticias" />
                    <h1 className="NotTitulo" > Últimas Notícias</h1>
                </div>

                <div className="itensNoticias">
                    <div href="" className="itemNoticia" id="itemNoticias1">
                        <div id="imagemNot1" className="imagemNoticias"></div>
                        <p className="descricaoNoticias">Mourão retoma atividades na segunda após 12 dias afastado</p>
                        <hr className="linha" />

                        <p className="dataNoticias">08/01/2021</p>
                    </div>

                    <div className="itemNoticia" id="itemNoticias2">
                        <div id="imagemNot2" className="imagemNoticias"></div>
                        <p className="descricaoNoticias">Governo da Bahia confirma caso de reinfecção do novo coronavírus</p>
                        <hr className="linha" />
                        <p className="dataNoticias">08/01/2021</p>
                    </div>

                    <div className="itemNoticia" id="itemNoticias3">
                        <div id="imagemNot3" className="imagemNoticias"></div>
                        <p className="descricaoNoticias">Seis segredos para o sucesso de Elon Musk</p>
                        <hr className="linha" />
                        <p className="dataNoticias">08/01/2021</p>
                    </div>

                    <div className="itemNoticia" id="itemNoticias4">
                        <div id="imagemNot4" className="imagemNoticias"></div>
                        <p className="descricaoNoticias">Saiba quais foram os 10 carros usados mais vendidos de 2020</p>
                        <hr className="linha" />
                        <p className="dataNoticias">08/01/2021</p>
                    </div>

                </div>
                <a  href="#" className="leiaMais"> LEIA MAIS</a>
            </div>
        )
    }
}